/* ================= Header ================= */

.email-optp-modle .heading-otp h2 {
  color: #ffffff;
  font-weight: 700;
  margin-bottom: 6px;
  text-align: center;
}

.email-optp-modle .heading-otp .set-lage {
  color: #ffffff;
  opacity: 0.9;
  margin-bottom: 16px;
  text-align: center;
}

/* ================= Verify + Resend ================= */

.email-optp-modle .verification-code {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
}

.email-optp-modle .verification-code p {
  margin: 0;
  font-weight: 600;
  color: #ffffff;
}

.email-optp-modle .resend-otp {
  color: var(--bs-secondary1);
  font-weight: 500;
}

/* ================= OTP Inputs ================= */

.email-optp-modle .otp-inputs {
  display: flex;
  justify-content: flex-start;
  gap: 14px;
  padding-left: 32px; /* desktop alignment */
  margin-bottom: 28px;
}

/* OTP & PIN boxes */
.email-optp-modle .otp-input,
.email-optp-modle .otp-input-login {
  width: 50px;
  height: 44px;
  border-radius: 10px;
  border: 2px solid #204c5e;
  background-color: transparent;
  /* color: #ffffff !important; */
  font-size: 18px;
  font-weight: 600;
  text-align: center;
  margin: 0;
  flex: 0 0 auto;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

/* Focus */
.email-optp-modle .otp-input:focus,
.email-optp-modle .otp-input-login:focus {
  border-color: var(--bs-secondary1);
  outline: none;
  box-shadow: 0 0 0 2px rgba(18, 89, 156, 0.35);
}

/* ================= PIN Sections ================= */

.email-optp-modle label {
  color: #ffffff;
  font-weight: 600;
  margin-bottom: 8px;
  display: block;
}

.email-optp-modle .d-flex.gap-2 {
  justify-content: flex-start;
  padding-left: 32px;
  margin-bottom: 26px;
}

/* ================= Submit Button ================= */

.email-optp-modle .btn-login {
  height: 48px;
  border-radius: 30px;
  font-weight: 600;
  background: linear-gradient(
    90deg,
    var(--bs-secondary1),
    var(--bs-primary3)
  );
  border: none;
  color: #ffffff;
}

/* Disabled */
.email-optp-modle .btn-login:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Spinner */
.email-optp-modle .btn-login .spinner-border {
  margin-left: 10px;
  width: 16px;
  height: 16px;
}

/* ================= Mobile Fix ================= */

@media (max-width: 480px) {
  .email-optp-modle .otp-input,
  .email-optp-modle .otp-input-login {
    width: 38px;
    height: 38px;
    font-size: 16px;
  }

  .email-optp-modle .otp-inputs,
  .email-optp-modle .d-flex.gap-2 {
    justify-content: center;
    padding-left: 0;
  }

  .email-optp-modle .modal-body {
    padding: 20px 16px;
  }
}
